/**
 * Mock implementation of the DatabaseService for testing purposes
 * This implementation does not rely on better-sqlite3
 */
export class DatabaseService {
  private isConnected: boolean = false;
  private dbPath: string = '';
  private mockData: Record<string, any[]> = {
    'aiService.prompts': [],
    'workbench.panel.aichat.view.aichat.chatdata': []
  };

  /**
   * Opens a connection to the database
   * @param dbPath Path to the database file
   */
  public async openConnection(dbPath: string): Promise<void> {
    this.dbPath = dbPath;
    this.isConnected = true;
    console.log(`Opened connection to database: ${dbPath}`);
    return Promise.resolve();
  }

  /**
   * Executes a query on the database
   * @param query SQL query to execute
   * @param params Query parameters
   * @returns Query results
   */
  public async executeQuery(query: string, params?: any[]): Promise<any[]> {
    if (!this.isConnected) {
      throw new Error('Database not connected');
    }

    // Simple mock implementation that returns predefined data based on query
    if (query.includes('aiService.prompts')) {
      return this.mockData['aiService.prompts'];
    } else if (query.includes('workbench.panel.aichat.view.aichat.chatdata')) {
      return this.mockData['workbench.panel.aichat.view.aichat.chatdata'];
    }
    
    return [];
  }

  /**
   * Closes the database connection
   */
  public async closeConnection(): Promise<void> {
    this.isConnected = false;
    console.log(`Closed connection to database: ${this.dbPath}`);
    return Promise.resolve();
  }

  /**
   * For testing purposes: set mock data
   */
  public setMockData(key: string, data: any[]): void {
    this.mockData[key] = data;
  }
} 