import * as fs from 'fs';
import * as path from 'path';
import * as os from 'os';
import { DatabaseService } from './databaseService';

export class CursorDataProvider {
  private static instance: CursorDataProvider;
  private cursorStoragePath: string | null = null;
  private databaseService: DatabaseService;

  private constructor() {
    this.databaseService = new DatabaseService();
    this.initializeStoragePath();
  }

  public static getInstance(): CursorDataProvider {
    if (!CursorDataProvider.instance) {
      CursorDataProvider.instance = new CursorDataProvider();
    }
    return CursorDataProvider.instance;
  }

  private initializeStoragePath(): void {
    const osType = os.platform();
    switch (osType) {
      case 'win32':
        this.cursorStoragePath = path.join(
          process.env.APPDATA || '',
          'Cursor',
          'User',
          'workspaceStorage'
        );
        break;
      case 'darwin':
        this.cursorStoragePath = path.join(
          os.homedir(),
          'Library',
          'Application Support',
          'Cursor',
          'User',
          'workspaceStorage'
        );
        break;
      case 'linux':
        this.cursorStoragePath = path.join(
          os.homedir(),
          '.config',
          'Cursor',
          'User',
          'workspaceStorage'
        );
        break;
      default:
        console.error(`Unsupported OS: ${osType}`);
        this.cursorStoragePath = null;
    }
  }

  public async findWorkspaceStorageFolders(): Promise<string[]> {
    if (!this.cursorStoragePath) {
      console.error('Cursor storage path not initialized');
      return [];
    }

    try {
      if (!fs.existsSync(this.cursorStoragePath)) {
        console.error(`Cursor storage path not found: ${this.cursorStoragePath}`);
        return [];
      }

      const workspaceFolders = fs.readdirSync(this.cursorStoragePath);
      
      // Filter to only include directories that contain a state.vscdb file
      const validWorkspaceFolders = workspaceFolders
        .map(folder => path.join(this.cursorStoragePath!, folder))
        .filter(folderPath => fs.existsSync(path.join(folderPath, 'state.vscdb')));
      
      return validWorkspaceFolders;
    } catch (error) {
      console.error(`Error finding workspace folders: ${error}`);
      return [];
    }
  }

  public async getChatData(): Promise<Record<string, any>[]> {
    const workspaceFolders = await this.findWorkspaceStorageFolders();
    const allChatData: Record<string, any>[] = [];

    for (const folderPath of workspaceFolders) {
      try {
        const dbPath = path.join(folderPath, 'state.vscdb');
        
        // Connect to the database
        await this.databaseService.openConnection(dbPath);

        // Query for chat data
        const aiPromptsQuery = `
          SELECT * FROM ItemTable 
          WHERE key LIKE '%aiService.prompts%'
        `;
        
        const workbenchChatQuery = `
          SELECT * FROM ItemTable 
          WHERE key LIKE '%workbench.panel.aichat.view.aichat.chatdata%'
        `;

        const aiPromptsData = await this.databaseService.executeQuery(aiPromptsQuery);
        const workbenchChatData = await this.databaseService.executeQuery(workbenchChatQuery);

        // Parse JSON data from database
        for (const row of [...aiPromptsData, ...workbenchChatData]) {
          try {
            if (row.value) {
              const parsedValue = JSON.parse(row.value);
              if (parsedValue) {
                allChatData.push({
                  source: row.key,
                  data: parsedValue,
                  workspace: path.basename(folderPath)
                });
              }
            }
          } catch (parseError) {
            console.error(`Error parsing JSON data: ${parseError}`);
          }
        }

        // Close the database connection
        await this.databaseService.closeConnection();
      } catch (error) {
        console.error(`Error processing workspace folder ${folderPath}: ${error}`);
      }
    }

    return allChatData;
  }
} 